import { Component, Prop, h } from '@stencil/core';

@Component({
  tag: 'ir-checkbox',
  styleUrl: 'ir-checkbox.css',
  shadow: true,
})
export class IrCheckbox {
  @Prop({ reflect: true }) checkboxId: string;
  @Prop({ reflect: true }) checkboxName: string;
  @Prop({ reflect: true, mutable: true }) checked: boolean;
  @Prop({ reflect: true }) checkboxStyle: string;
  render() {
    return <input class={this.checkboxStyle} checked={this.checked} type="checkbox" name={this.checkboxName} id={this.checkboxId} aria-label={'checkbox' + this.checkboxId} />;
  }
}
